export default {
	data() {
		return {
			hasPassword:true,
			oldpassword:"",
			newpassword:"",
			renewpassword:"",
			disabled:true,
			loading:false
		}
	},
	onLoad(e) {
		this.hasPassword = !!(e.password && e.password !== 'false');
	},
	watch:{
		oldpassword(val){
			this.change();
		},
		newpassword(val){
			this.change();
		},
		renewpassword(val){
			this.change();
		}
	},
	methods: {
		// 监听输入框
		change(){
			if(this.hasPassword && !this.oldpassword){
				return this.disabled = true;
			}
			if(this.newpassword && this.renewpassword){
				return this.disabled=false;
			}
			this.disabled=true;
		},
		//验证层
		check(){
			if(this.hasPassword && (!this.oldpassword || this.oldpassword == "")){
				uni.showToast({
					title: '旧密码有误',
					icon: "none"
				});
				return false
			}
			if(!this.newpassword || this.newpassword == ""){
				uni.showToast({
					title: '新密码有误',
					icon: "none"
				});
				return false
			}
			if(!this.renewpassword || this.renewpassword == ""){
				uni.showToast({
					title: '确认密码有误'
				});
				return false
			}
			if(this.newpassword !== this.renewpassword){
				uni.showToast({
					title: '新密码与确认密码不一致',
					icon: "none"
				});
				return false
			}
			return true;
		},
		//提交
		async submit(){
			if(!this.check()) return;
			this.loading = this.disabled = true;
			let [err,res] = await this.$http.post('/repassword',{
				oldpassword:this.oldpassword || 0,
				newpassword:this.newpassword,
				renewpassword:this.renewpassword,
			},{
				token: true,
				checkToken: true
			})
			if(!this.$http.errorCheck(err,res)){
				this.loading = this.disabled = false;
				return;
			}
			// 修改状态，缓存
			this.User.userinfo.password = true;
			uni.setStorageSync('userinfo',this.User.userinfo);
			this.loading = this.disabled = false;
			return uni.showToast({
				title: '修改密码成功！',
				success: () => {
					uni.navigateBack({ delta: 1 });
				}
			});
		}
	}
}
